:root {
  /* Modern Minimalist Palette */
  --color-emerald-600: #059669;
  --color-emerald-700: #047857;
  --color-emerald-100: #D1FAE5;
  --color-gray-900: #111827;
  --color-gray-500: #6B7280;
  --color-gray-100: #F3F4F6;
  --color-white: #FFFFFF;

  /* Mapping Legacy Variables to New Theme (Backward Compatibility) */
  --primary: var(--color-emerald-600);
  /* Was Gold #C5A059 */
  --primary-dark: var(--color-emerald-700);
  /* Was #9A7B3E */
  --primary-light: var(--color-emerald-100);
  /* Was #E6CFA0 */
  --primary-gradient: linear-gradient(135deg, var(--color-emerald-600) 0%, var(--color-emerald-700) 100%);

  --secondary: var(--color-gray-900);
  /* Was Charcoal #1A1C20 */
  --secondary-light: var(--color-gray-500);
  /* Was #2C3038 */
  --secondary-gradient: linear-gradient(135deg, var(--color-gray-900) 0%, var(--color-gray-500) 100%);

  --accent: #F59E0B;
  /* Amber for accent instead of Red */
  --success: #10B981;

  --background: #FAFAFA;
  /* Clean off-white */
  --surface: #FFFFFF;
  --surface-glass: rgba(255, 255, 255, 0.95);
  /* Less transparent */
  --surface-dark: #1F2937;
  /* Gray-800 */

  --text-main: var(--color-gray-900);
  --text-muted: var(--color-gray-500);
  --text-light: #FFFFFF;
  --text-on-primary: #FFFFFF;

  /* Spacing (Keep consistent) */
  --space-xs: 0.25rem;
  --space-sm: 0.5rem;
  --space-md: 1rem;
  --space-lg: 1.5rem;
  --space-xl: 2rem;
  --space-2xl: 3rem;
  --space-3xl: 4rem;

  /* Shadows (Softer, more diffuse) */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-glow: 0 0 15px rgba(5, 150, 105, 0.3);

  /* Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
  --radius-full: 9999px;

  /* Typography */
  --font-heading: 'Inter', sans-serif;
  --font-body: 'Inter', sans-serif;

  --text-xs: 0.75rem;
  --text-sm: 0.875rem;
  --text-base: 1rem;
  --text-lg: 1.125rem;
  --text-xl: 1.25rem;
  --text-2xl: 1.5rem;
  --text-3xl: 1.875rem;
  --text-4xl: 2.25rem;
  --text-5xl: 3rem;
}

/* Utilities */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

